import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import { DataService } from "../data.service";
import { ruas_rdp } from "../../environments/ruas-rdp";
import { ruas_jd } from "../../environments/ruas-jd";

@Component({
  selector: 'app-folder',
  templateUrl: './folder.page.html',
  styleUrls: ['./folder.page.scss'],
})
export class FolderPage implements OnInit {
  public folder: string;
  public congregacao: string;
  public goalList: string[];
  public goalListOriginal: string[];

  constructor(private router: Router, private route: ActivatedRoute, private data: DataService) { }

  ngOnInit() {
    this.folder = 'Ruas do Território';
    this.congregacao = this.route.snapshot.paramMap.get('congregacao');
    this.data.changeCongregacao(this.congregacao);

    if(this.congregacao === 'Jardim Violeta') {
      this.goalList = ruas_jd.sort();
    } else if (this.congregacao === 'Rio da Prata') {
      this.goalList = ruas_rdp.sort();
    }

    this.goalListOriginal = this.goalList;
  }

  filterList(evt) {
    const searchTerm = evt.srcElement.value;
    
    if (!searchTerm) {
      this.goalList = this.goalListOriginal;
      return;
    }
  
    this.goalList = this.goalList.filter(currentGoal => {
      if (currentGoal && searchTerm) {
        if (currentGoal.toLowerCase().indexOf(searchTerm.toLowerCase()) > -1) {
          return true;
        }
        return false;
      }
    });
  }

  go(item) {
    this.router.navigate(['/telefones', item]);
  }
}
